<template>
    <div class="together">
    <div class="word">
        <h3>网页扫雷游戏</h3>
            <div class="difficulty">
                <a href="javascript:void(0)" @click="level(1)">基础</a>
                <a href="javascript:void(0)" @click="level(2)">中级</a>
                <a href="javascript:void(0)" @click="level(3)">专家</a>
                <a href="javascript:void(0)" @click="level(4)">满屏</a>
                <a href="javascript:void(0)" @click="custom()">自定义</a>
            </div>
    </div>
    <gridComponent ref="gc" :mineChange="this.mine" :height="this.height" :width="this.width"></gridComponent>
    <div id="score">
        <scoreComponent></scoreComponent>
    </div>
</div>
</template>

<script>
import gridComponent from './../grid/grid-component.vue';
import scoreComponent from './../score/score-component.vue'
export default {
    data() {
        return {
            mine: undefined,
            width: null,
            height: null, 
            scores: null
        }
    },
    methods: {
    draw() {
        this.$refs.gc.draw();
    },
    restart() {
        this.$refs.gc.restart();
    },
    level(l) {
    let SW = document.body.clientWidth;
    
    if (l === 1) {
        this.mine.tr = 9;
        this.mine.td = 9;
        this.mine.mineCount = 10;
    } else if (l === 2) {
        this.mine.tr = 16;
        this.mine.td = 16;
        this.mine.mineCount = 40;
    } else if (l === 3) {
        //console.log(SW, SH);
        if (SW >= 755) {
            this.mine.tr = 30;
            this.mine.td = 16;
            this.mine.mineCount = 99;
        } else {
            this.mine.tr = 16;
            this.mine.td = 30;
            this.mine.mineCount = 99;
        }
    } else if (l === 4) {
        let M;
        let X = parseInt((SW - 18) / 25);
        let Y = parseInt((1000 - 54) / 25);
        let XY = X * Y;
        if (XY >= 480) {
            M = XY * 0.20625;
        } else {
            M = XY * XY / 5760 + XY / 8;
        }
        M = parseInt(M);
        this.mine.tr = X;
        this.mine.td = Y;
        this.mine.mineCount = M;
    }
    // this.width = grid.width;
    // this.height = grid.height;
    // console.log(this.width, this.height,"w")
    this.setShape(this.mine.tr, this.mine.td)
    // this.mine.init();
    // this.draw()
    this.restart();
},
    custom() {
        const td = prompt("请输入行数", "15");
        this.mine.td = parseInt(td? td : 9);
        console.log(this.mine.td)
        const tr = prompt("请输入列数", "15");
        this.mine.tr = parseInt(tr? tr : 9);
        const mineCount = prompt("请输入雷数", "20")
        this.mine.mineCount = parseInt(mineCount? mineCount : 10);
        this.setShape(this.mine.tr, this.mine.td)
        // this.mine.init();
        // this.draw()
        this.restart();
    },
    setShape(tr, td) {
        let game = document.getElementsByClassName("game");
        let cell = document.getElementsByClassName("cell");
        let grid = document.getElementById("grid");
        this.scores = this.$el.querySelector("#score");

        game[0].style.width = `${tr*25+4}px`;
        game[0].style.height = `${td*25+44}px`;
        cell[0].style.width = `${tr*25}px`;
        cell[0].style.height = `${td*25}px`;
        grid.width = `${tr*25}`;
        grid.height = `${td*25}`;
        this.width = grid.width;
        this.height = grid.height;
        this.scores.style.marginTop = `${td*25+65}px`;
    }
    },
    components: {
        gridComponent,
        scoreComponent
    },
    mounted() {
        let mines = this.$mine;
        this.mine = mines.methods.createMine(9, 9, 10);
        
    }
}

</script>

<style scoped>
.difficulty {
    display: flex;
    justify-content: center;
}
.difficulty a {
    padding: 0 5px;
}
#score {
    padding-top: 15px;
    margin-top: 290px;
    /* display: flex;
    justify-self: center; */
    width: 100%;
    position: relative;

}
#score a {
    position: absolute;
    left: 50%;
    transform: translate(-50%);
}
/* .together {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
} */
</style>